import React, { Component } from 'react';
import {addMember} from '../../actions/dutyAction';
import {connect} from 'react-redux';
import PropTypes from 'prop-types'

class Addmembers extends Component {
 constructor(){
     super()
     this.state={
        servicenumber:''
     }
 }
   addmemberClick=(e)=>{
       e.preventDefault();
       const datal= {
           servicenumber:this.state.servicenumber
       }
       
      // datal.append("servicenumber",this.state.servicenumber);
       this.props.addMember(datal);
       /*console.log(this.state.servicenumber);
      this.props.addMember(data);*/
   }
   onChange=(e)=>{
       this.setState({[e.target.name]:e.target.value})
   }

  render() {
    return (
        <div >
      <div className="container-fluid">
      <h4><strong>Add Personel</strong></h4>
        <div className="card">
        <div className="card-body">
          <div className="row">
          <form onSubmit={this.addmemberClick}>
                <div className="col-md-8">
                  <input value={this.state.servicenumber} onChange={this.onChange} name="servicenumber" placeholder="enter the service number you want to add" type="text" className="form-control"/>
                </div>
                <div className="col-md-8">
                   <button type="submit" className="btn btn-success">Add Personel</button>
                </div>
           </form>
            </div>
        </div>
        </div>
      </div>
      </div>
    )
  }
}
Addmembers.propTypes={
   
}

const mapStateToProps=(state)=>({

})

export default connect(mapStateToProps,{addMember})(Addmembers)