import React, { Component } from 'react';
import  {BrowserRouter as Router, Route ,Switch} from  'react-router-dom';
import {/*setCurrent,*/ setCurrentUser, logoutUser} from './actions/authActions';
import jwt_decode from 'jwt-decode';
import setAuthToken from './utils/setAuthToken';


import {Provider} from 'react-redux';//provider provides a store that hold our state
//private routes must be rapped in a switch compnent or else it will not show the content of the route
import store from './store'
import Addmembers from './components/assignduty/Addmembers'
import Content from './components/layout/Content';
import NavBar from './components/layout/NavBar';
import Footer from './components/layout/Footer';
import Landing from './components/layout/Landing';
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import Sidebar from './components/layout/Sidebarcontent/Sidebar';
import Dashboard from './components/dashboard/Dashboard';
import PrivateRoute from './components/common/PrivateRoute';
import CreateProfile from './components/profile/CreateProfile';
import Vieprofile from './components/profile/Viewprofile';
import Duty from './components/assignduty/Duty';
import AssignedDuty from './components/assignedDuty/AssignedDuty';
import addAdmin from './components/assignduty/addAdmin';
import logout from './components/auth/logout'
import './App.css';



if(localStorage.jwtToken){
  setAuthToken(localStorage.jwtToken);
  // Decode token and get user infor and exp
  const decoded = jwt_decode(localStorage.jwtToken);

  //Set user and isAuthenticated
  store.dispatch(setCurrentUser(decoded));
//console.log(store);

  //check for expired token
  const currentTime = Date.now()/1000;
  if(decoded.exp < currentTime){
    store.dispatch(logoutUser());
    //TODO: clear currnt Profile 

   // store.dispatch(clearCurrentProfile(decoded));
    //Redirect to  login
  
    window.location.href= '/login';
  }
}




class App extends Component {
  componentWillMount(){
    
  }
  render() {
    return (
  <Provider store={store}>
  <Router>
    <div className="App" >

        
         <div className="wrapper ">
         <Sidebar/>
         <div className="main-panel">
          
         <NavBar />
         <Switch><PrivateRoute exact path='/assignedduty' component={AssignedDuty}/></Switch>
         <Switch><PrivateRoute exact path='/addmember' component={Addmembers}/></Switch>
         <Switch><PrivateRoute exact path='/assignduty' component={Duty}/></Switch>
         <Switch><PrivateRoute exact path='/dashboard' component={Dashboard}/></Switch>
         <Switch><PrivateRoute exact path='/createprofile' component={CreateProfile}/></Switch>
       {/*  <Switch><PrivateRoute exact path='/addadmin' component={addAdmin}/></Switch>*/}
         <Switch><PrivateRoute exact path='/profile' component={Vieprofile}/></Switch>
         
         <Route exact path='/' component={Landing}></Route>
        <Route exact path='/login' component={Login}></Route>
        <Route exact path='/register' component={Register}></Route>
        <Route exact path='/logout' component={logout}></Route>
       
         <Footer/>
         </div>
         
         
          </div>
        {/* <!-- Landing -->*/}
        
         

          
        { /*<!-- Footer -->*/}
         
     
     </div>
  </Router>
  </Provider>
    );
  }
}

export default App;
