import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import Content from '../layout/Content'
import './layout.css';


class Landing extends Component {
  componentWillMount(){
    if(this.props.auth.isAuthenticated){
      this.props.history.push('/dashboard');
    }
   }

  render() {

    return (
<div className="landing-page">     
<nav class="navbar navbar-expand-md bg-dark navbar-dark mynav">
 
<a class="navbar-brand" href="#">
    <img src="./img/logo.png" alt="logo"/>
 </a>

  <ul class="navbar-nav ul-nav">
    <li class="nav-item">
      <Link class="nav-link oo" to="/">Home</Link>
    </li>
    <li class="nav-item">
      <Link class="nav-link" to="/login">Login</Link>
    </li>
    <li class="nav-item">
      <Link class="nav-link" to="/register">Register</Link>
    </li>
  </ul>
</nav>
<div className="container">
   <div className="row row1">

      <div className="col-md-12 text">
           <h1>
             Welcome To Nigerian Defence Academy Staff Duty Allocation Portal
           </h1>
           <p style={{color:"yellow"}}>Allocate duty, check assignments etc..</p>
     <Link className="btn btn-success bt" to="/login">Login</Link>
     <Link to="/register" className="btn btn-success bt">Register</Link>
</div>
   </div>
</div>
</div>        
       
    )
  }
}
Landing.propTypes={
  errors:PropTypes.object.isRequired
}
const mapStateToProps=(state)=>({
   auth:state.auth,
   errors:state.errors
})
export default connect(mapStateToProps,{})(Landing)