import React, { Component } from 'react';
import isEmpty from '../../validation/is-Empty';
import Addpersonnel from './Addpersonnel';
import getallduties from '../../actions/dutyAction'

export default class ViewDuty extends Component {
  constructor(){
    super()
    this.state={
       addpernel:false
    }
  }

AddpersonnelClick=()=>{
  this.setState({addpernel:!this.state.addpernel})
}
onCancel=()=>{
  this.setState({addpernel:!this.state.addpernel})
 
}
onSave=()=>{

}

  render() {
    const {duty}=this.props;
    //console.log(duty)
    let Personels;
    let addsoldier;
    if(this.state.addpernel){
      addsoldier=(<Addpersonnel duty={duty} onCancel={this.onCancel}/>)
    }else{
      addsoldier=("")
    }
    
    if(!isEmpty(duty.dutypersonnel)){
      let x=0;
         Personels=duty.dutypersonnel.map(personnel=>
             <tr key={personnel.user}>
               <td>{x=x+1}</td>
               <td>{personnel.name}</td>
               <td>{personnel.armofservice}</td>
               <td>{personnel.rank}</td>
               <td>{personnel.servicenumber}</td>
               <td>{personnel.dutylocation}</td>
               <td>{personnel.from}</td>
               <td>{personnel.to}</td>
               
             </tr>
         )
    }else{Personels=(<tr><td>No Officer/Personel has been assigned this duty yet</td></tr>)}
    return (
      <div>
      <div style={{position:"absolute",  top:"10%", zIndex:"425",opacity:"0.99"}} className="container">
      <div style={{height:"50%", width:"90%", overflowY:"scroll"}} className="card">
          <div className="card-header">
             <h4><strong>{duty.dutytitle}</strong></h4><span>From: <strong>{duty.from}</strong> To: <strong>{duty.to}</strong> Base: <strong>{duty.dutybase}</strong> Location: <strong>{duty.dutylocation}</strong></span>
          </div>
          <div className="card-body">
          <h5>Personels</h5>
          <div className="row">
          <div className="col-md-12">
            <table  className="table-bordered">
              <thead>
                <tr>
                  <td><strong>Serial Num</strong></td>
                  <td><strong>Name</strong></td>
                  <td><strong>Arm Of Serv</strong></td>
                  <td><strong>Rank</strong></td>
                  <td><strong>Service Number</strong></td>
                  <td><strong>Duty Location</strong></td>
                  <td><strong>From</strong></td>
                  <td><strong>To</strong></td>
                </tr>
              </thead>
              <tbody>
              {Personels}
              </tbody>
               
            </table>
          </div>
          <div className="row">
          <div className="col-md-6">
               <button onClick={this.AddpersonnelClick} className="btn btn-success">+ Add</button>
              
          </div>
          <div className="col-md-2">
          <button onClick={this.props.oncancelClick} className="btn btn-warning">+ Close</button>
          </div>
          </div>
          </div>
        </div>
        </div>
      </div>
      {addsoldier}
      </div>
    )
  }
}
