import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../auth/auth.css';
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import { logoutUser } from '../../actions/authActions';

 class NavBar extends Component {

   logoutUserfunc=()=>{
    this.props.logoutUser();
   }
  render() {
    const {isAuthenticated,user}= this.props.auth
    const iSayYes=false;
let navbar;
    if(isAuthenticated){
       navbar=(
        <nav className="navbar navbar-expand-lg navbar-transparent navbar-absolute fixed-top ">
        <div className="container-fluid contfluid">
          <div className="navbar-wrapper">
            <a className="navbar-brand" href="#pablo">Welcome</a>
          </div>
          <button className="navbar-toggler" type="button" data-toggle="collapse" aria-controls="navigation-index" aria-expanded="false" aria-label="Toggle navigation">
            <span className="sr-only">Toggle navigation</span>
            <span className="navbar-toggler-icon icon-bar"></span>
            <span className="navbar-toggler-icon icon-bar"></span>
            <span className="navbar-toggler-icon icon-bar"></span>
          </button>
          <div className="collapse navbar-collapse justify-content-end">
            <form className="navbar-form">
              <div className="input-group no-border">

              </div>
            </form>
            <ul className="navbar-nav">
            <li className="nav-item dropdown">
                <a className="nav-link" href="http://example.com" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  <i className="material-icons">message</i>
                  <span className="notification">0</span>
                  <p className="d-lg-none d-md-block">
                    Some Actions
                  </p>
                </a>
                <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownMenuLink">
 
                </div>
              </li>
              <li className="nav-item dropdown">
                <a className="nav-link" href="http://example.com" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  <i className="material-icons">notifications</i>
                  <span className="notification">0</span>
                  <p className="d-lg-none d-md-block">
                    Some Actions
                  </p>
                </a>

              </li>
              <li className="nav-item dropdown">
                <a className="nav-link" href="#pablo" id="navbarDropdownProfile" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                 {user.name} <i className="material-icons">person</i>
                  <p className="d-lg-none d-md-block">
                    Account
                  </p>
                </a>
                <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownProfile">
                  <a className="dropdown-item" href="#">Profile</a>
                  <a className="dropdown-item" href="#">Settings</a>
                  <div className="dropdown-divider"></div>
                  <Link to='/logout' onClick={this.logoutUserfunc} className="dropdown-item" >Log out</Link>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </nav>
       )
    }else if(iSayYes){

    
       navbar=("")}

    return (
      <div style={{marginBottom:"50px"}}>
        {navbar}
      </div>
    )
  }
}
NavBar.propTypes={
  errors:PropTypes.object.isRequired,
  logoutUser:PropTypes.func.isRequired
}
const mapStateToProps=(state)=>({
  auth:state.auth,
  errors:state.errors
})
export default connect(mapStateToProps,{logoutUser})(NavBar)