import React, { Component } from 'react';
import TextFormField from '../common/TextFormField';
import SelectformField from '../common/SelectformField';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {createduty} from '../../actions/dutyAction';
import isEmpty from '../../validation/is-Empty'


 class Adddutyform extends Component {
  constructor(){
      super()
      this.state={
          errors:'',
          dutytitle:'',
          dutybase:'',
          from:'',
          to:''
      }
  }



    onChange=(e)=>{
      this.setState({[e.target.name]:e.target.value});
     isEmpty(this.state.errors[e.target.name])?this.setState({errors:''}) : this.state.errors[e.target.name]="";
      //this.state.errors[e.target.name]="";
    }
    submit=(e)=>{
      e.preventDefault();
      const dutyData={
        dutytitle:this.state.dutytitle,
        dutybase:this.state.dutybase,
        from:this.state.from,
        to:this.state.to
      }
    this.props.createduty(dutyData);
    //this.props.cancel();
    }
    componentWillReceiveProps(nextprops){
      const {errors,duties} =nextprops;
      if(isEmpty(errors.dutytitle)&&isEmpty(errors.from)&&isEmpty(errors.dutylocation)&&isEmpty(errors.dutybase)){
        alert("duty successfully added !")
        this.props.cancel();
      }
      this.setState({errors:errors});
       // console.log("check ",duty.duty);
    }

  render() {
    const dutytitle=['','Field Officer','Duty Officer','Orderly Warrant Officer','Orderly Staff Sergeant','Fire Fighting BNS'];
    const dutybase=["","NDA Afaka","NDA Ribadu Campus"];


    return (
    <div style={{position:"absolute", top:"10%", zIndex:"423",opacity:"0.95"}} className="container">
      <div className="card">
          <div className="card-header">
             <h4>New Duty Assignment</h4>
          </div>
          <div className="card-body">
        
         <div className="row">
          <div className="col-md-12">
             <SelectformField
             onChange={this.onChange}
             error={this.state.errors.dutytitle}
             label="Duty Title"
             value={this.state.dutytitle}
             name="dutytitle"
             >
                 {dutytitle}
             </SelectformField>
          </div>
          </div>
          <div className="row">
            <div className="col-md-6">
                 <SelectformField
                    onChange={this.onChange}
                    error={this.state.errors.dutybase}
                    label="Duty Base"
                    value={this.state.dutybase}
                    name="dutybase"
                    >
                        {dutybase}
                    </SelectformField>
                 </div>
             </div>
             <div className="row">
                 <div className="col-md-6">
                 <label>From:</label>
                    <TextFormField
                    type="date"
                    value={this.state.from}
                    name="from"
                    onChange={this.onChange}
                    error={this.state.errors.from}
                    />
                 </div>
                 <div className="col-md-6">
                 <label>To:</label>
                 <TextFormField
                    type="date"
                    
                    value={this.state.to}
                    name="to"
                    onChange={this.onChange}
                    error={this.state.errors.to}
                    />
                 </div>
             </div>
             <div className="row">

               <div className="col-md-12">
                 <button  onClick={this.submit} className="btn btn-warning">+ Save</button>
                 <button onClick={this.props.cancel} className="btn btn-danger">x Cancel</button>
               </div>

             </div>
             
          </div>
      </div>
    </div>
    )
  }
}
Adddutyform.propTypes={
errors:PropTypes.object.isRequired,
createduty:PropTypes.func.isRequired,
duties:PropTypes.array.isRequired
}
const mapStateToProps=(state)=>({
    errors:state.errors
})

export default connect(mapStateToProps,{createduty})(Adddutyform)