import {GET_DUTY, GET_USERS, GET_DUTIES,DUTY_LOADING,CLEAR_CURRENT_DUTY,PERSONEL_LOADING} from '../actions/types'
const initialState = {
    duty:[],
    duties: [],
    users:[],
    loading: false,
    errors:''
}
export default function(state=initialState,action){
switch(action.type){
    case DUTY_LOADING:
    return{
        ...state,
        loading:true
    }
    case PERSONEL_LOADING:
    return{
        ...state,
        loading:true
    }
    case GET_USERS:
    return{
        ...state,
        users:action.payload,
        loading:false
    }
    case GET_DUTIES:
    return{
      ...state, 
      duties:action.payload,
      loading:false,
      errors:{}
    }
    case GET_DUTY:
    return{
    ...state,
    duty:action.payload,
    loading:false
}
case CLEAR_CURRENT_DUTY:
return{
    ...state,
    profile:null
}
    default:
    return state;

}}