import React, { Component } from 'react';
import '../auth/auth.css';
import classnames from 'classnames'

export default class TextFormField extends Component {
  render() {
    return (
    <div className="form-group">
      <label className="bmd-label-floating" htmlFor={this.props.name}>{this.props.label}</label>
      <select value={this.props.value} onChange={this.props.onChange}  name={this.props.name} 
      className={classnames("form-control",{'is-invalid':this.props.error})}
      id={this.props.name} >
      {this.props.children.map(data=><option key={data} value={data} name={data}>{data}</option>)}
      </select>

      {this.props.info && <small className="form-text text-muted">{this.props.info}</small>}
      {this.props.error &&  <div className="invalid-feedback"><strong className="err">{this.props.error}</strong></div>}
      
    </div>
    )
  }
}
