import React, { Component } from 'react'
import {Link} from 'react-router-dom';

export default class Auth extends Component {
  render() {
    return (
      <div>
     <div className="card lcard">
      <div className="card-body lcardb">
       <div className="row lcardb">
          <div  className="col-md-4 col4 cside">
            <span>
                <h3 id="h3" style={{paddingLeft:"20px", marginTop:`${this.props.sa}`, color:"rgb(24, 161, 24)"}}>{this.props.auth}</h3>
                <Link to='/' className="navbar-brand"><img   src="/img/logo.png" alt="nda logo"/> </Link>
                <p id="p" style={{textAlign:"center",marginBottom:`${this.props.sb}`}}>Duty Allocation Portal for Milliatary Staff</p>
            </span>
          </div>
          <div className="col-md-8 col8 form-child">
               {this.props.children}
          </div>
       </div>
       </div>
       </div>
      </div>
    )
  }
}
