//import {TEST_DISPATCH} from '../actions/types';
import {NAV_STATE} from '../actions/types'


const currentnav = {
    nav:null
}
export default function(state = currentnav, action) {
    switch(action.type) {
        case NAV_STATE:
        return {
           ...state,
            nav:action.payload
        }
        
        /*case TEST_DISPATCH:
        return {
            ...state,
            user: action.payload
        }*/
        default:
        return state;
    }
}