import React, { Component } from 'react';
import './auth.css';
import Auth from './auth';//jsx component
import TextFormField from '../common/TextFormField'
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router-dom';
import {registerUser} from '../../actions/authActions'
import isEmpty from '../../validation/is-Empty';
import {Link} from 'react-router-dom';
import Content from '../layout/Content';
import SelectformField from '../common/SelectformField'

 class Register extends Component {
    constructor(){
        super()
        this.state={
            servicenumber:'',
            armofservice:'',
            name:'',
            password:'',
            password2:'',
            errors:''
        }
    }
   componentWillMount(){
    if(this.props.auth.isAuthenticated){
      this.props.history.push('/dashboard');
    }
   }
    componentWillReceiveProps(nextProps){
      if(nextProps.errors){
        this.setState({errors:nextProps.errors})
      }
    }

    onChange=(e)=>{
     e.preventDefault();
     this.setState({[e.target.name]:e.target.value})
    
     if(!isEmpty(this.state.errors[(e.target.name)])){
      
     this.state.errors[(e.target.name)]=""
    
       }
    }
    onSubmit=(e)=>{
        e.preventDefault()
       const data={
            servicenumber:this.state.servicenumber,
            name:this.state.name,
            password:this.state.password,
            password2:this.state.password2,
            armofservice:this.state.armofservice
        }
        console.log(data.armofservice);
        this.props.registerUser(data,this.props.history);

        //console.log(data);
    }



  render() {
      const {errors} = this.state
      //console.log(errors);
    return (
      <div className="landing-page">     
      <nav class="navbar navbar-expand-md bg-dark navbar-dark mynav">
       
      <a class="navbar-brand" href="#">
          <img src="./img/logo.png" alt="logo"/>
       </a>
      
        <ul class="navbar-nav ul-nav">
          <li class="nav-item">
            <Link class="nav-link oo" to="/">Home</Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/login">Login</Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/register">Register</Link>
          </li>
        </ul>
      </nav>
     <div style={{overflow:"hidden"}} className="container formcontainer">
        <Auth sa="50px" sb="198px" auth="Register">
        <form style={{overflow:"hidden"}} onSubmit={this.onSubmit}>
 
         <TextFormField error={errors.name} label="Name:" onChange={this.onChange} type="text" name="name" value={this.state.name} placeholder="Please Enter Your Name(Last Name and Initials)"/>

         <TextFormField error={errors.servicenumber} label="Service Number:" onChange={this.onChange} type="text" name="servicenumber" value={this.state.servicenumber} placeholder="Enter Your Service Number"/>

        <SelectformField error={errors.armofservice} name="armofservice" onChange={this.onChange} value={this.state.armofservice} label="Arm Of Service">
           {[
             null,
             "Army",
             "AirForce",
             "Navy"
            ]}
        </SelectformField>

         <TextFormField error={errors.password} label="Password:" onChange={this.onChange} type="password" name="password" value={this.state.password} placeholder="Enter Your password"/>

         <TextFormField error={errors.password2} label="Password2:" onChange={this.onChange} type="password" name="password2" value={this.state.password2} placeholder="Re-Enter Your password"/>

            <button type="submit" className="btn btn-success btn-block">Register</button>
           <Link to="/login">Registered ? Login</Link>
         </form>

        </Auth>
     </div>
     </div>
    )
  }
}

Register.propTypes={
    registerUser:PropTypes.func.isRequired,
    errors:PropTypes.object.isRequired,
    auth:PropTypes.object.isRequired
}

const mapStateToProps=(state)=>({
  errors:state.errors,
  auth:state.auth
})
export default connect(mapStateToProps,{registerUser})(withRouter(Register))