import React, { Component } from 'react';
import {Link} from 'react-router-dom'

export default class Noprofile extends Component {
  render() {
    return (

        <div className="row">
            <div className="col-md-4">

            </div>
            <div className="col-md-4">
               <div className="card">
                   <div className="card-body">
                    <h2>User Profile Not Fount</h2>
                    <p>You must Create a profile to continue</p>
                    <Link to="/createprofile" className="btn btn-primary btn-block --primary">Create Profile</Link>
                   </div>
               </div>
            </div>
            <div className="col-md-4">

            </div>
        </div>
    
    )
  }
}
