import React, { Component } from 'react'
//import {Link} from 'react-router-dom';
import './auth.css';
import TextFormField from '../common/TextFormField';
import Auth from './auth';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {loginUser} from '../../actions/authActions';
import isEmpty from '../../validation/is-Empty'
import {withRouter} from 'react-router-dom';
import Content from '../layout/Content';
import {Link} from 'react-router-dom';



class Login extends Component {
    constructor(props){
        super(props)
       this.state={
        servicenumber:'',
            password:'',
            errors:''
        }
    }
    componentDidMount(){
      if(this.props.auth.isAuthenticated){
        this.props.history.push('/dashboard');
      }
    }
    componentWillReceiveProps(nextProps){
      this.setState({errors:nextProps.errors})
       // console.log(this.state.errors);
        if(nextProps.auth.isAuthenticated){
          this.props.history.push('/dashboard');
        }
        if(nextProps.errors){
          this.setState({errors: nextProps.errors})
        }
    }
    onChange = (e) => {
       // this.setState({errors:""});
        this.setState({[e.target.name]:e.target.value});
        if(!isEmpty(this.state.errors[(e.target.name)])){
       
          this.state.errors[(e.target.name)]=""
         
            }

        }
    onSubmit=(e)=>{
      e.preventDefault();
      const loginData={
        servicenumber:this.state.servicenumber,
        password:this.state.password
      }
      this.props.loginUser(loginData,this.props.history)

    }
  render() {
    const {errors}= this.state;

    return (
      <div className="landing-page">     
      <nav class="navbar navbar-expand-md bg-dark navbar-dark mynav">
       
      <a class="navbar-brand" href="#">
          <img src="./img/logo.png" alt="logo"/>
       </a>
      
        <ul class="navbar-nav ul-nav">
          <li class="nav-item">
            <Link class="nav-link oo" to="/">Home</Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/login">Login</Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/register">Register</Link>
          </li>
        </ul>
      </nav>
     <div className="container formcontainer">
        <Auth auth="Login">
        <form onSubmit={this.onSubmit} >
        <br/>
            <TextFormField error={errors.servicenumber} label="Service Number:" onChange={this.onChange} type="text" name="servicenumber" value={this.state.name} placeholder="Enter Your Service Number"/>

            <TextFormField error={errors.password} label="Password:" onChange={this.onChange} type="password" name="password" value={this.state.password} placeholder="Enter Your password"/>
             <br/>
            <button type="submit" className="btn btn-success btn-block">Login</button>
            <Link to="/register"> {" "}Not Registered ? Register</Link>
         </form>
        </Auth>
      </div>
     </div>
    )
  }
}

Login.propTypes={
  loginUser:PropTypes.func.isRequired,
  errors:PropTypes.object.isRequired
}

const mapStateToProps=(state)=>({
 auth:state.auth,
 errors:state.errors
})
export default connect(mapStateToProps,{loginUser})(withRouter(Login));