import React, { Component } from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {addPersonelToduty,getAllUsers,getallduties} from '../../actions/dutyAction';
import SelectformField from '../common/SelectformField';


class Addpersonnel extends Component {
    constructor(){
        super()
        this.state={
            personelform:false,
            personel:'',
            personelId:'',
            armofservice:'',
            dutylocation:'',
            servicenumber:'',
            to:'',
            from:''
        }
    }
    onCancel=()=>{
        this.props.onCancel();
        this.props.getallduties();
    }
componentWillMount(){
    this.props.getAllUsers();
}
componentDidMount(){
    //console.log(this.props);
}
selectPersonel=()=>{
      this.setState({personelform:!this.state.personelform})
    }
onChange=(e)=>{
    this.setState({[e.target.name]:e.target.value});
}
  personeladded=(user)=>{
      //console.log(user);
      this.setState({
           personel:user.name,
           personelId:user._id,
           armofservice:user.armofservice,
           servicenumber:user.servicenumber,
           personelform:!this.state.personelform
      })
    }
    onSave=(e)=>{
       e.preventDefault();
       const personelToAdd={
           id:this.props.duty._id,
           name:this.state.personel,
           armofservice:this.state.armofservice,
           servicenumber:this.state.servicenumber,
           dutylocation:this.state.dutylocation,
           user:this.state.personelId,
           from:this.state.from,
           to:this.state.to
       }
       this.props.addPersonelToduty(personelToAdd);

    }

  render() {
const {users}=this.props.users;
const dutylocation=['','SSW','MedCen','DOF','DOL','AR','AW','Demo Bn','Mog Bn','Aby Bn','Dal Bn','Bur Bn','Ash Bn','NW','Coord','PT Wing,DMT','WPC','NDA Pro','92 MR','CB','AFW','Cadts Mess','PT','Sig','Band','WT','ETT'];
//console.log(users.users)
    let personel;
    this.state.personelform ? personel =(
        <div style={{position:"fixed",top:"10%",zIndex:"9879", width:"70%"}} className="container-fluid">
           <div className="card">
           <div className="card-header">
               <h4>Select Personnel</h4>
           </div>
              <div className="card-body">
               {/*  <div className="row">
                    <div className="col-md-12">
                     <div className="form-check-inline">
                           <label className="form-check-label" htmlFor="army">
                            <input type="checkbox" className="form-check-input" id="army" name="army" value="army" />army
                        </label>
                        </div>
                        <div className="form-check-inline">
                        <label className="form-check-label" htmlFor="airforce">
                            <input type="checkbox" className="form-check-input" id="airforce" name="airforce" value="airforce"/>airforce
                        </label>
                        </div>
                        <div className="form-check-inline">
                        <label className="form-check-label">
                            <input value="navy" type="checkbox" className="form-check-input"/>Navy
                        </label>
                        <button onClick={this.selectPersonel} className="btn btn-danger">X Close</button>
                        </div>
                      </div>
                 </div>
                 <div className="row">
                        <div className="col-md-12">
                            <input placeholder="Filter Search Personnel" className="form-control" type="text" />
                        </div>
                 </div>*/}
                 
                 <div className="row">
                  <div className="col-md-12">
                   
                  {users.map(user=>(
                      <div key={user._id} className="list-group">
                          <button onClick={()=>this.personeladded(user)} className="list-group-item list-group-item-action list-group-item-success">
                          
                          {user.name} | {user.servicenumber} | {user.armofservice}
                          </button>
                     </div>)
                    
                  )}
                  </div>
                 </div>
              </div>
           </div>
        </div>
    ) : personel=('');

    return (
        <div>
            {personel}
      <div className="container-fluid" style={{zIndex:"427",position:"fixed", width:"80%", top:"10%"}}>
     
        <div className="card">
        <h4 style={{textAlign:"center"}}>Add personnels</h4>
               <div className="card-body">
               <form onSubmit={this.onSave}>
                   <div className="row">
                       <div className="col-md-4">
                         <p><input required value={this.state.personel} disabled/></p>
                         <button onClick={this.selectPersonel} className="btn btn-success">Select Personnel</button>
                       </div>
                       <div className="col-md-4">
                         <p><input onChange={this.onChange}  required type="date" name="from"/></p>
                         <p>Date From</p>
                       </div>
                       <div className="col-md-4">
                       <p><input onChange={this.onChange} type="date" name="to" required/></p>
                         <p>Date To</p>
                      </div>
                      <div className="row">
                          <div className="col-md-4">

                            <SelectformField
                            onChange={this.onChange}
                            label="Duty Location"
                            value={this.state.dutylocation}
                            name="dutylocation"
                            >
                                {dutylocation}
                            </SelectformField>

                            </div>
                            <div className="col-md-4">
                                <button type="submit" className="btn btn-sucess">Save</button>
                          </div>
                          <div className="col-md-4">
                               <button onClick={this.onCancel} className="btn btn-warning">Close</button>
                          </div>
                      </div>
                   </div>
                </form>
               </div>
        </div>
      </div>
    </div>
    )
  }
}

Addpersonnel.propTypes={
    errors:PropTypes.object.isRequired,
    users:PropTypes.object.isRequired,
    getAllUsers:PropTypes.func.isRequired,
    addPersonelToduty:PropTypes.func.isRequired,
    getallduties:PropTypes.func.isRequired
}

const mapStateToProps =(state)=>({
    errors:state.errors,
    users:state.duty
})
export default  connect(mapStateToProps,{getAllUsers,addPersonelToduty,getallduties})(Addpersonnel)