import React, { Component } from 'react';
import {withRouter} from  'react-router-dom';
import PropTypes from 'prop-types';
import {createprofile} from '../../actions/profileAction';
import {connect} from 'react-redux';
import {setnavstate} from '../../actions/setnavstate';
import TextFormField from '../common/TextFormField';
import SelectformField from '../common/SelectformField';


 class CreateProfile extends Component {
     constructor(){
         super()
        this.state={
              avatar:null,
              rank:'',
              email:'',
              mobilenumber:'',
              errors:''
         }
     }
onChange=(e)=>{
   if(e.target.name==="avatar"){
    this.setState({avatar:e.target.files[0]})
   // console.log(e.target.files[0]); 
   }else{
    this.setState({[e.target.name]:e.target.value});
    this.setState({errors:""})
   }

  
}

    componentDidMount(){
        
        this.props.setnavstate("createprofile");
     }
     componentWillReceiveProps(nexProps){
        const {errors}=nexProps;
        this.setState({errors:errors})
     }
     onSubmit=(e)=>{
       e.preventDefault();

       const data = new FormData() 
       data.append('avatar', this.state.avatar);
       data.append('rank', this.state.rank);
       data.append('email', this.state.email);
       data.append('mobilenumber', this.state.mobilenumber);

      // console.log("this is the data i got",data)
       this.props.createprofile(data, this.props.history);
     }
  render() {
    const {user}= this.props.auth;
    
   let armofservice=user.armofservice;
   //console.log(this.state);
    //const {errors}=this.props
    let rank=["r"," "];
    if (armofservice==="Army"){
        rank=["Select Rank >","Recruit","Private","Lance Corporal","Corporal","Sergeant","Staff Sergeant","Warrant Officer","Master Warrant Officer",
            "Army Warrant Officer","2nd Lieutenant","ieutenant","Captain","Major","Lieutenant Colonel","Colonel","Brigadier General","Major General","Lieutenant General",
            "General"]
    }else if(armofservice==="Navy"){
        rank=["Select Rank >","Trainee","Ordinary Seaman","Able Seaman","Leading Seaman","Petty Officer","Warrant Officer","Master Warrant Officer","Navy Warrant Officer",
            "Midshipman","Sub-Lieutenant","Lieutenant","Lieutenant Commander","Commander","Captain","Commodore","Rear Admiral","Vice Admiral","Admiral"]
    }else if(armofservice==="AirForce"){
       rank=["Select Rank >","Aircraftman","Lance Corporal","Corporal","Sergeant","Flight Sergeant","Warrant Officer","Master Warrant Officer","Air Warrant Officer",
        "Pilot Officer","Flying Officer","Flight Lieutenant","Squadron Leader","Wing Commander","Group Captain","Air Commodore","Air Vice-Marshal","Air Marshal",
            "Air Chief Marshal"];
    }  



    return (
        <div className="container-fluid">
        <div className="row">
        <div className="col-md-2">

        </div>
          <div className="col-md-8">
            <div className="card">
              <div className="card-header card-header-primary">
                <h4 className="card-title">Edit Profile</h4>
                <p className="card-category">Complete your profile</p>
              </div>
              <div className="card-body">
                <form onSubmit={this.onSubmit} >
                  <div className="row">
                    <div className="col-md-12">
                    <TextFormField
                  value={this.state.email}
                  name="email"
                  onChange={this.onChange}
                  label="Email"
                  error={this.state.errors.email}
                  type="email"

                  />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-6">
                  <TextFormField
                  value={this.state.mobilenumber}
                  name="mobilenumber"
                  onChange={this.onChange}
                  label="Phone"
                  error={this.state.errors.mobilenumber}
                  />
                    </div>
                    <div className="col-md-6">
                <div className="form-control">
                <label htmlFor="avatar">Profile Image</label>
                <input
                type="file"
                 id="avatar"
                 name="avatar"
                 onChange={this.onChange}
                  />
                   <div style={{color:"red",fontWeight:"400"}}>{this.state.errors.file} </div>
                  </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-12">
                      <SelectformField
                      onChange={this.onChange}
                      value={this.state.rank}
                      label=""
                       name="rank"
                      error={this.state.errors.rank}
                      >
                          {rank}
                      </SelectformField>
                    </div>
                  </div>

                  <button type="submit" className="btn btn-primary pull-right">Update Profile</button>
                  <div className="clearfix"></div>
                </form>
              </div>
            </div>
          </div>

        </div>
      </div>
    )
  }
}



CreateProfile.propTypes={
   errors:PropTypes.object.isRequired,
   profile:PropTypes.object.isRequired,
   createprofile:PropTypes.func.isRequired ,

   setnavstate:PropTypes.func.isRequired
}

const mapStateToProps=(state)=>({
auth:state.auth,
profile:state.profile,
errors:state.errors
})

export default  connect(mapStateToProps,{createprofile,setnavstate})(withRouter(CreateProfile))