import React, { Component } from 'react';
import {setnavstate} from '../../actions/setnavstate';
import PropTypes from 'prop-types';
import {connect} from'react-redux';
//import {withRouter} from 'react-router-dom';
import Adddutyform from './Adddutyform'
import {getallduties} from '../../actions/dutyAction';
//import Dutyflex from './Dutyflex';
import Spinner from '../common/Spinner'
import DutyFeed from './Dutyfeed';
//import {withRouter} from 'react-router-dom'

class Duty extends Component {
  constructor(){
    super()
    this.state={
      addform:false
    }
   // this.duties={};
  }

    componentWillMount(){
        this.props.setnavstate("assignduty");
        this.props.getallduties();
     }

     addFormonClick=(e)=>{
       this.setState({addform:!this.state.addform});
     }
     cancel=(e)=>{
      this.setState({addform:!this.state.addform});
     }
     addDutyClick=()=>{
      this.setState({addform:!this.state.addform});
     }

  render() {
    let adddutyform;
    let showduties;
    const {duties,loading} =this.props.duties;
    if(loading){
      showduties=<Spinner/>
    }else{
      showduties=<DutyFeed duties={duties}/>;

      //console.log(duties[0]);
    }
   

    if(this.state.addform){
      adddutyform=( <Adddutyform duties={duties} cancel={this.cancel} onClick={this.addFormonClick}/>)
    }else{adddutyform=("")}
    
    //let duty;


    return (
      <div>
        {adddutyform}
       <div className="container-fluid">
        <div className="row">
        <div className="col-md-2">
        </div>
          <div className="col-md-8">
            <div className="card">
              <div className="card-header card-header-primary">
                <h4 className="card-title">ASSIGN DUTY</h4>
                <p className="card-category">Assign Duty To Officers and Personels</p>
              </div>
              <div className="card-body">
                <div className="row">
                  <button onClick={this.addDutyClick} className="btn btn-warning"><i className="material-icons">
                    library_add
                    </i> New Duty</button>
                </div>
              </div>
              </div>
            </div>
          </div>
        </div>
       <div className="container-fluid">
           {showduties}
       </div>
      </div>
    )
  }
}
Duty.propTypes={
    errors:PropTypes.object.isRequired,
    setnavstate:PropTypes.func.isRequired,
    getallduties:PropTypes.func.isRequired,
    duties:PropTypes.object.isRequired
 }

const mapStateToProps=(state)=>({
    auth:state.auth,
    errors:state.errors,
    duties:state.duty
    })
    
    export default  connect(mapStateToProps,{setnavstate,getallduties})(Duty)