import React, { Component } from 'react'
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';
import {getCurrentProfile} from '../../../actions/profileAction';
import isEmpty from '../../../validation/is-Empty'


class Sidebar extends Component {
  constructor(){
    super()
    this.state={
      navstate:'dashboard'
    }
  }
componentWillReceiveProps(nextprops){
  
   const {nav}=nextprops.nav;
   this.setState({navstate:nav});
   console.log("the current state is " ,this.state.navstate);
}
componentWillMount(){
  this.props.getCurrentProfile();
}

  render() {
    const {isAuthenticated} = this.props.auth;
    const {profile}=this.props.profile;
    console.log(profile);
    let adminpreviledge;
  if(!isEmpty(profile)){
    if(profile.admin){
      adminpreviledge=(
        <div>
           <li className={`nav-item ${this.state.navstate=="assignduty"?"active":("")}`}>
             <Link className="nav-link" to="/assignduty">
               <i className="material-icons">group_add</i>
               <p>Assign Duty</p>
             </Link>
           </li>
           <li className="nav-item ">
             <Link className="nav-link" to="/addmember">
               <i className="material-icons">person_add</i>
               <p>Add Officers/Personels</p>
             </Link>
           </li>
          {/** <li className="nav-item ">
             <a className="nav-link" href="./rtl.html">
               <i className="material-icons">transfer_within_a_station</i>
               <p>Add/Transfer Admin</p>
             </a>
           </li> */}
      </div>
     )
    }
  }else{adminpreviledge=("")}
     

    let sidebar;
    if(!isAuthenticated){
       sidebar="";
    }else{
     sidebar=(
<div  className="sidebar" data-color="green" data-background-color="white" data-image="../assets/img/sidebar-1.jpg">
      {/*<!--
        Tip 1: You can change the color of the sidebar using: data-color="purple | azure | green | orange | danger"

        Tip 2: you can also add an image using data-image tag
    -->*/}
      <div className="logo">
        <a href="http://www.creative-tim.com" className="simple-text logo-normal">
          <img src="../img/logo.png"/>
        </a>
        
      </div>
      <div className="sidebar-wrapper">
        <ul className="nav">
          <li className={`nav-item ${this.state.navstate=="dashboard"?"active":("")}`}>
            <Link className="nav-link"to="/dashboard">
              <i className="material-icons">dashboard</i>
              <p>Dashboard</p>
            </Link>
          </li>
          <li className={`nav-item ${this.state.navstate=="viewprofile" ? "active":("")} `}>
            <Link className="nav-link"to="/profile">
              <i className="material-icons">person</i>
              <p>User Profile</p>
            </Link>
          </li>
          <li className={`nav-item ${this.state.navstate=="createprofile"?"active":("")} `}>
            <Link className="nav-link" to="/createprofile">
              <i className="material-icons">settings</i>
              <p>Edit Profile</p>
            </Link>
          </li>
          <li className={`nav-item ${this.state.navstate=="assignedduty" ? "active":("")} `}>
            <Link className="nav-link" to="/assignedduty">
              <i className="material-icons">assignment_ind</i>
              <p>Assigned Duties</p>
            </Link>
          </li>

        {adminpreviledge}
        </ul>
      </div>
    </div>
     )
    }
    return (
      <div>
        {sidebar}
      </div>
    )
  }
}

Sidebar.propTypes={
 auth:PropTypes.object.isRequired,
 errors:PropTypes.object.isRequired,
 nav:PropTypes.object.isRequired,
 getCurrentProfile:PropTypes.func.isRequired,
 profile:PropTypes.object.isRequired
}

const mapStateToProps=(state)=>({
auth:state.auth,
errors:state.errors,
profile:state.profile,
nav:state.nav
})
export default connect(mapStateToProps,{getCurrentProfile})(Sidebar);