import axios from 'axios';

import {GET_DUTY,PERSONEL_LOADING,DUTY_LOADING,GET_DUTIES, CLEAR_CURRENT_DUTY, GET_USERS, GET_ERRORS, SET_CURRENT_USER} from './types';
//import { userInfo } from 'os';

export const getallduties =()=>dispatch =>{
dispatch(setDutyLoading());
axios.get('/api/dutyposts')
.then(res=>{
    //console.log(res);
    dispatch({
        type:GET_DUTIES,
        payload: res.data
    })
})
.catch(err=>dispatch({
    type:GET_DUTIES,
    payload: {}
}))
};



export const createduty =(dutyData)=>dispatch =>{

   axios.post('/api/dutyposts', dutyData)
   .then(res=>{
    //history.push('/dashboard');
    dispatch(
       getallduties()
    )
   })
   .catch(err =>{
       dispatch({
           type:GET_ERRORS,
           payload: err.response.data
       })
   })
}

export const deleteDuty = (duty) => dispatch => {

    if(window.confirm('Are you sure? : This can NOT be undone!')){
        dispatch(setDutyLoading())
         axios.delete(`/api/dutyposts/delete/${duty}`)
         .then(duties=>{dispatch(getallduties())
           alert(duties.data.msg);
        })
         .catch(err=>dispatch({
            type:GET_ERRORS,
            payload: err.response.data
         }))
    }

}

export const addPersonelToduty=(data)=>dispatch=>{
    dispatch(setAddPersonnelLoading())
    axios.post('/api/admin/allocateduty/addpersonel',data)
    .then(res=>{
        dispatch(getallduties());
        alert("personell added successfully");
    }).catch(err=>{
        alert("an error occured");
    })
}

export const getAllUsers=()=>dispatch=>{
    axios.get('/api/users/all')
    .then(res=>{
        dispatch({
            type:GET_USERS,
            payload:res.data
        })
    })
    .catch(err=>{
       dispatch({
      type:GET_ERRORS,
      payload:err.response.data
     })
    })
}
export const addMember=(member)=>dispatch=>{
    console.log(member)
     axios.post('/api/admin/newuser',member)
    
     .then(res=>{
         alert("Added Successfully")
     }).catch(err=>{
         alert("an error occured")
     })
}
export const setDutyLoading =()=>{
    return {
        type: DUTY_LOADING
    }
    
}

export const setAddPersonnelLoading =()=>{
    return {
        type: PERSONEL_LOADING
    }
    
}
