import React, { Component } from 'react';
import Dutyflex from './Dutyflex';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {deleteDuty} from '../../actions/dutyAction';
import ViewDuty from './ViewDuty';
import isEmpty from '../../validation/is-Empty';

 class Dutyfeed extends Component {
    constructor(){
      super()
      this.dview='';
      this.state={
        viewduty:''
      }
     
    }
    deleteduty=(duty)=>{
       //console.log(duty);
       this.props.deleteDuty(duty._id);
    }

    cancelClick=()=>{
      this.setState({viewduty:''})
    }

    editduty=(duty)=>{
  
    console.log("am hit")
    this.setState({viewduty:(<ViewDuty oncancelClick={this.cancelClick} duty={duty}/>)})

    }
  

  render() {

   
      const {duties}=this.props;
      const duty=duties.map(duty=><Dutyflex editduty={()=>this.editduty(duty)} onClick={()=>this.deleteduty(duty)} duty={duty}/>)

    return (
      <div className="row">
        {this.state.viewduty}
        {duty}
      </div>
    )
  }
}
Dutyfeed.propTypes={
 errors:PropTypes.object.isRequired,
 duties:PropTypes.array.isRequired,
 deleteDuty:PropTypes.func.isRequired
}

const mapStateToProps=(state)=>({
errors:state.errors

})

export default connect(mapStateToProps,{deleteDuty})(Dutyfeed)