import React, { Component } from 'react';
import isEmpty from '../../validation/is-Empty'

export default class Dutyflex extends Component {
  render() {
     //let d=new this.props.duty.from;
    return (
      
       <div key={this.props.duty._id} className="col-md-4">
            <div className="card">
              <div className="card-header card-header-primary">
                <h4 className="card-title">{this.props.duty.dutytitle}</h4>
                <p className="card-category">details :</p>
            </div>
            <div className="card-body">
                <div style={{borderBottom:"1px solid #ccc",borderTop:"1px solid #ccc"}} className="row">
                     <div className="col-md-6 col-sm-6 col-6">
                          <h5>location :</h5>
                        <p><strong>{this.props.duty.dutylocation}</strong></p>
                       
                     </div>
                     <div className="col-md-6 col-sm-3 col-4">
                     <h5>base :</h5>
                        <p><strong>{this.props.duty.dutybase}</strong></p>
                    </div>
                </div>
                <div style={{borderBottom:"1px solid #ccc"}} className="row">
                <div className="col-md-6  col-sm-6 col-6">
                <h5>From :</h5>
                    <p><strong>{this.props.duty.from}</strong></p>
                </div>
               <div className="col-md-6  col-sm-3 col-4">
                         <h5>To :</h5>
                        <p><strong>{!isEmpty(this.props.duty.to)?this.props.duty.to:"Not Specified"}</strong></p>
                 </div>
               </div>
               <div style={{overflow:"hidden"}} className="row">
                   <div className="col-md-5 col-lg-6 col-sm-4 col-6">
                         <button onClick={()=>this.props.editduty(this.props.duty)} className="btn btn-primary">View/Edit</button>
                   </div>
                   <div className="col-md-1 col-lg-4 col-sm-2 col-4">
                   <button onClick={()=>this.props.onClick(this.props.duty)} className="btn btn-danger">X Delete </button>
                    </div>
               </div>
            </div>
          </div>
         </div>
       
    )
  }
}
