import React, { Component } from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import Content from '../layout/Content';
import {getCurrentProfile} from '../../actions/profileAction'
import Spinner from '../common/Spinner';
import isEmpty from '../../validation/is-Empty';
import Noprofile from '../profile/Noprofile';
import {setnavstate} from '../../actions/setnavstate';
import {Link} from 'react-router-dom';

class Dashboard extends Component {
    constructor(){
        super()
    }

  componentDidMount(){
    const {isAuthenticated}=this.props.auth;
    this.props.setnavstate("dashboard");
    //const {profile}=this.props;
  if(isAuthenticated){
     this.props.getCurrentProfile();
   }
   else{this.props.history.push('/login')}
  } 
  componentWillReceiveProps(nextProps){

  } 
  render() {
    const style={
      backgroundColor:"yellow",
      fontWeight:"350",
      cursor:"pointer",
      borderRadius:"50px"
    }
   const {auth}=this.props;
   const {loading,profile}=this.props.profile;
   let src;
   let details
   if(loading){
       src="https://via.placeholder.com/150"
       details=(<Spinner/>)
   }else{
      
       if(!isEmpty(profile)){
        src=`/image/${profile.avatar}`;
           //src=profile.avatar;

           details=(
      
            <div className="container-fluid">
            <div className="row">
            <div className="col-md-12">
            <div className="card">
             <div className="card-body">
             <div className="row">
             <div className="col-md-10">
                <h3 style={{color:"#28a745"}}>{auth.user.name}</h3>
                <p style={{color:"#28a745"}}>Service Number:{auth.user.servicenumber}</p>
                </div>                   
            <div  className="col-md-2">
            <img style={{borderRadius:"50%",height:"100px",width:"100px"}} id="photo" src={src}/>
            </div>
             </div>
{/**         <Switch><PrivateRoute exact path='/assignedduty' component={AssignedDuty}/></Switch>
         <Switch><PrivateRoute exact path='/addmember' component={Addmembers}/></Switch>
         <Switch><PrivateRoute exact path='/assignduty' component={Duty}/></Switch>
         <Switch><PrivateRoute exact path='/dashboard' component={Dashboard}/></Switch>
         <Switch><PrivateRoute exact path='/createprofile' component={CreateProfile}/></Switch>

         <Switch><PrivateRoute exact path='/profile' component={Vieprofile}/></Switch> */}
            </div>
            </div>
            </div>
            </div>
           <div className="row">
             <div className="col-md-4">
                <div className="card">
                <div className="card-header card-header-primary">
                <h4 className="card-title">Edit Profile</h4>
                <Link style={style} to="/createprofile" >Complete your profile</Link>
              </div>
              <div className="card-body">
              </div>
                </div>
             </div>
             <div className="col-md-4">
                <div className="card">
                <div className="card-header card-header-primary">
                <h4 className="card-title">View Profile</h4>
                <Link style={style} to="/profile" className="card-category">View Your Profile</Link>
              </div>
              <div className="card-body">
              </div>
                </div>
             </div>
             <div className="col-md-4">
                <div className="card">
                <div className="card-header card-header-primary">
                <h4 className="card-title">Assignments</h4>
                <Link style={style} to="/assignedduty" className="card-category">View your Assignments</Link>
              </div>
              <div className="card-body">
              </div>
                </div>
             </div>
           </div>
           <div className="row">

             <div className="col-md-4">
                <div className="card">
                <div className="card-header card-header-primary">
                <h4 className="card-title">Logout</h4>
                <Link style={style} to="/logout" className="card-category">Logout</Link>
              </div>
              <div className="card-body">
              </div>
                </div>
             </div>
           </div>
            </div>
          
           )

       }else{
        src="https://via.placeholder.com/150"
        details=(
            <div className="container-fluid">
            <div className="row">
                <div className="col-md-8">
                <h1>{auth.user.name}</h1>
                <h1></h1>
                </div>
            <div  className="col-md-4">
            <img style={{borderRadius:"50%",height:"150px",width:"150px"}} id="photo" src={src}/>
            </div>
            </div>
            <Noprofile/>
            </div>
             )
       }
   }//ends else if not loading
   
   
console.log(this.props);
    return (
        <Content>
        
          {details}
         
          </Content>
       )
  }
}
Dashboard.propTypes={
    auth:PropTypes.object.isRequired,
    errors:PropTypes.object.isRequired,
    profile:PropTypes.object.isRequired,
    getCurrentProfile:PropTypes.func.isRequired,
    setnavstate:PropTypes.func.isRequired
}
const mapStateToProps=(state)=>({
    auth:state.auth,
    errors:state.errors,
    profile:state.profile
})
export default connect(mapStateToProps,{getCurrentProfile,setnavstate})(Dashboard)