import React, { Component } from 'react';
import {getallduties} from '../../actions/dutyAction';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import Dutyflex from '../assignduty/Dutyflex';
import Spinner from '../common/Spinner';
import Showdutydetails from './Showdutydetails';

class AssignedDuty extends Component {
    constructor(){
        super()
        this.state={
            shouldview:false,
            duty:''
        }
    }
    componentWillMount(){
        this.props.getallduties();
    }

    view=(duty)=>{
        //sconsole.log('an hit')
      this.setState({shouldview:!this.state.shouldview,
            duty:duty
        });
        //console.log(duty);
    }
    oncloseClick=()=>{
        this.setState({shouldview:!this.state.shouldview});
    }

  render() {
      let dutyview;
      let showDetails;
      const {duties,loading}=this.props.duties;
       const {user}=this.props.user;

     if(this.state.shouldview){
         showDetails=(
            <Showdutydetails user={user} oncloseClick={this.oncloseClick} duty={this.state.duty}/>
        )
         }else{
          showDetails=('')
         }

      
 
       if(loading){
        dutyview=(<Spinner/>)
       }else{
       if(duties){
         dutyview =duties.map(duty=>
            duty.dutypersonnel.map(users=>
                //<Dutyflex duty={duty}/>
              users.user==user.id?<Dutyflex editduty={()=>this.view(duty)} duty={duty}/>:('')
               /* if(users.user==user.id){
                    view= <Dutyflex duty={duty}/>
                }*/
           )
        )
       }else{
           dutyview=("");
           console.log("no duties")
       }
    } 
       //console.log(duties,user);
    return (
      <div>
        <div>
        {showDetails}
       <div className="container-fluid">
        <div className="row">
        <div className="col-md-2">
        </div>
          <div className="col-md-8">
            <div className="card">
              <div className="card-header card-header-primary">
                <h4 className="card-title">ASSIGNED DUTY</h4>
                <p className="card-category">View Duties Assigned To You</p>
              </div>
              <div className="card-body">
                <div className="row">
                  <button onClick={this.addDutyClick} className="btn btn-warning"><i className="material-icons">
                    message
                    </i> Suggestion</button>
                </div>
              </div>
              </div>
            </div>
          </div>
        </div>

      </div>
      <div className="container-fluid">
       <div className="row">
          
          {dutyview}
          
          </div>
       </div>
      </div>
    )
  }
}
AssignedDuty.propTypes={
   errors:PropTypes.object.isRequired,
   getallduties:PropTypes.func.isRequired,
   duties:PropTypes.object.isRequired,
   user:PropTypes.object.isRequired
}

const mapStateToProps=(state)=>({
errors:state.errors,
duties:state.duty,
user:state.auth
})

export default connect(mapStateToProps,{getallduties})(AssignedDuty)